import {Card,
        CardBody,
        CardTitle, 
        CardSubtitle, 
        Badge, 
        Collapse, 
        Button, 
        Row, 
        Col, 
        Modal, 
        ModalHeader, 
        ModalBody, 
        ModalFooter,
        InputGroup,
        InputGroupText,
        Input
      } from "reactstrap";
import Table from 'react-bootstrap/Table';
import React, { useState, useEffect } from 'react';
import Pagination from "./Pagination";
import Map from '../dashboard/Map';
import { AddBeaconData,DelTrackingData } from '../../store';
import { useDispatch, useSelector } from "react-redux"
import TrafficLights from "../../assets/images/users/TrafficLight.png";

const TrackingTables = (args) => {
  let TrackingData = useSelector((state)=> state.TrackingData);
  let dispatch = useDispatch();

  const [openIndex, setOpenIndex] = useState(null);
  const [modal, setModal] = useState(false);
  const toggle = () => setModal(!modal);

  const toggleCollapse = (index) => {
    if (openIndex === index) {
      setOpenIndex(null);
    } else {
      setOpenIndex(index);
    }
  };

  useEffect(()=>{

  },[TrackingData])


  return (
    <div>
      <Card>
        <CardBody>
          <CardTitle tag="h5"><b>Tracking service</b></CardTitle>
          <CardSubtitle className="mb-2 text-muted" tag="h6">
            <div style={{ display: 'flex', alignItems: 'center' }}>
              <span className="p-2 bg-danger rounded-circle d-inline-block ms-3" />
              <h6>
                <Badge color="secondary">Unread</Badge>
              </h6>
              <span className="p-2 bg-warning rounded-circle d-inline-block ms-3" />
              <h6>
                <Badge color="secondary">Read</Badge>
              </h6>
              <span className="p-2 bg-success rounded-circle d-inline-block ms-3" />
              <h6>
                <Badge color="secondary">Completed</Badge>
              </h6>
            </div>
          </CardSubtitle>
          <Table className="no-wrap mt-3 align-middle" responsive borderless>
            <thead>
              <tr>
                <th style={{textAlign:'center'}}>신호등</th>
                <th style={{textAlign:'center'}}>시간</th>
                <th style={{textAlign:'center'}}>상태</th>
                <th style={{textAlign:'center'}}>주소</th>
                <th style={{textAlign:'center'}}></th>
              </tr>
            </thead>
            <tbody>
              {TrackingData.map((tdata, index) => (
                <React.Fragment key={index}>
                  <tr className="border-top" style={{textAlign:'center'}}>
                    <td>
                      <div className="d-flex align-items-center p-2" style={{textAlign:'center'}}>
                        <img
                          src={tdata.img}
                          className="rounded-circle"
                          alt="trafficlight"
                          width="45"
                          height="45"
                        />
                        <div className="ms-3">
                          <span className="text-muted" >{tdata.uuid}</span>
                        </div>
                      </div>
                    </td>
                    <td>{tdata.time}</td>
                    <td>
                      {tdata.status === "pending" ? (
                        <span className="p-2 bg-danger rounded-circle d-inline-block ms-3"></span>
                      ) : tdata.status === "holt" ? (
                        <span className="p-2 bg-warning rounded-circle d-inline-block ms-3"></span>
                      ) : (
                        <span className="p-2 bg-success rounded-circle d-inline-block ms-3"></span>
                      )}
                    </td>
                    <td>{tdata.address}</td>
                    <td>
                    <button
                        onClick={() => toggleCollapse(index)}
                        className="btn btn-link"
                      >
                        {openIndex === index ? <i class="bi bi-toggle-on bi-2x"/> : <i class="bi bi-toggle-off bi-2x"/>}
                      </button>
                    </td>
                  </tr>
                  <tr>
                    <td colSpan="6">
                      <Collapse isOpen={openIndex === index}>
                        <Row>
                          <Col>
                            <div
                              style={{
                                width: '500px', // 원하는 너비 설정
                                height: '300px', // 원하는 높이 설정
                                border: '1px solid #ccc', // 테두리 스타일 설정
                                position: 'relative'
                              }}>
                                {openIndex === index && <Map initlatitude={tdata.latitude} initlongitude={tdata.longitude} latitude={tdata.latitude} longitude={tdata.longitude} zoom={18} />}
                            </div>
                          </Col>
                          <Col>
                            <Row className="p-4">
                              <Table striped bordered hover>
                                <thead>
                                  <tr>
                                    <th className="text-center">UUID</th>
                                    <th className="text-center">Latitude</th>
                                    <th className="text-center">Longitude</th>
                                  </tr>
                                </thead>
                                <tbody>
                                  <tr>
                                    <td className="text-center">{tdata.uuid}</td>
                                    <td className="text-center">{tdata.latitude}</td>
                                    <td className="text-center">{tdata.longitude}</td>
                                  </tr>
                                </tbody>
                              </Table>
                            </Row>

                            <Row>
                              <div className="d-flex justify-content-center">
                                <Button className="me-3" color="primary" onClick={toggle}>
                                  입력
                                </Button>
                                <Modal isOpen={modal} toggle={toggle} {...args}>
                                  <ModalHeader toggle={toggle}>등록</ModalHeader>
                                  <ModalBody>
                                    <InputGroup className="mb-4">
                                      <InputGroupText>
                                        Latitude
                                      </InputGroupText>
                                      <Input />
                                    </InputGroup>

                                    <InputGroup className="mb-4">
                                      <InputGroupText>
                                        Longitude
                                      </InputGroupText>
                                      <Input />
                                    </InputGroup>

                                    <InputGroup className="mb-4">
                                      <InputGroupText>
                                        another
                                      </InputGroupText>
                                      <Input />
                                    </InputGroup>
                                  </ModalBody>
                                  <ModalFooter>
                                    <Button
                                      color="primary"
                                      onClick={() => {
                                        const confirmRegistration = window.confirm("정말 등록하시겠습니까?");
                                        if (confirmRegistration) {
                                          dispatch(
                                            AddBeaconData({
                                              img: tdata.img,
                                              uuid: tdata.uuid,
                                              time: tdata.time,
                                              status: tdata.status,
                                              address: tdata.address,
                                              latitude: tdata.latitude,
                                              longitude: tdata.longitude,
                                            })
                                          );
                                          alert("등록되었습니다.");
                                          dispatch(
                                            DelTrackingData(tdata.uuid)
                                          );
                                          toggle();
                                        }
                                      }}>
                                      등록
                                    </Button>{' '}
                                    <Button color="secondary" onClick={toggle}>
                                      취소
                                    </Button>
                                  </ModalFooter>
                                </Modal>
                                <Button
                                  color="danger"
                                  onClick={()=>{
                                    const confirmRegistration = window.confirm("정말 삭제하시겠습니까?");
                                    if (confirmRegistration) {
                                      dispatch(
                                        DelTrackingData(tdata.uuid)
                                      );
                                      alert("삭제되었습니다.");
                                    }
                                  }}
                                >삭제</Button>
                              </div>
                            </Row>
                          </Col>
                        </Row>
                      </Collapse>
                    </td>
                  </tr>
                </React.Fragment>
              ))}
            </tbody>
          </Table>
        </CardBody>
        <Pagination></Pagination>
      </Card>
    </div>
  );
};

export default TrackingTables;
