import React, { useEffect } from "react";

const Map = (props) => {
    useEffect(() => {
        // 네이버 지도 API를 비동기적으로 불러오기
        const script = document.createElement("script");
        script.src = `https://openapi.map.naver.com/openapi/v3/maps.js?ncpClientId=${process.env.NAVER_MAPS_API_KEY}`;
        script.async = true;
        document.head.appendChild(script);

        // API 스크립트가 로드되면 맵을 초기화합니다.
        script.onload = () => {
            const map = new window.naver.maps.Map("map", {
                center: new window.naver.maps.LatLng(
                    props.initlatitude || 37.579382,
                    props.initlongitude || 126.977059
                ), // 초기 지도 중심 좌표
                zoom: props.zoom || 12, // 초기 줌 레벨
            });

            // 마커 추가를 위한 좌표값 넣는 부분
            const markers = [
                { lat: props.latitude || null, lng: props.longitude || null }
            ];

            markers.forEach((markerCoords) => {
                new window.naver.maps.Marker({
                    position: new window.naver.maps.LatLng(
                        markerCoords.lat,
                        markerCoords.lng
                    ),
                    map: map,
                });
            });
        };
    }, []);

    return (
        <div id="map" style={{ width: "100%", height: "100%"}}></div>
    );
};

export default Map;
